import Vue from 'vue'
import Router from 'vue-router'
import login from './../page/login.vue'
import register from './../page/register.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/',
      name: 'register',
      component: register
    }

  ]
})



