<template>
  <div id="app">
    <transition name='slider-fade' mode='out-in'>
      <router-view/>
    </transition>
  </div>
</template>

<script>
export default {
  name: "App"
};
</script>

<style>
*{
  margin: 0;padding: 0;
}
.slider-fade-enter-active {
  transition: opacity 0.3s ease;
}
.slider-fade-leave-active {
  transition: opacity 0.3s ease;
}
.slide-fade-enter,
.slide-fade-leave-active {
  opacity: 0;
}

.main {
  margin-top: 3rem;
}
</style>



