<template>
  <div class="Login">
    <div class="logo">
      <img src="https://static.segmentfault.com/v-59410548/global/img/logo-b.svg">
    </div>
    <div class="content">
      <div class="phone">
        <input type="tel" placeholder="手机号" v-model="mobile">
      </div>

      <div class="password">
        <input type="password" autocomplete="new-password" placeholder="密码" v-model="password">
      </div>
    </div>

    <div class="regBtn">
      <button @click="login">登陆</button>
    </div>

    <div class="thirdWay">
      <div class="line"></div>
      <div class="loginBtn">第三方账号登陆</div>
      <div class="line"></div>
    </div>

    <div class="thirdWayLogo" @click="showTips">
      <div><img src="http://oqjgod7s1.bkt.clouddn.com/weixinsf.png"></div>
      <div><img src="http://oqjgod7s1.bkt.clouddn.com/qqsf.png"></div>
      <div><img src="http://oqjgod7s1.bkt.clouddn.com/weibosf.png"></div>
      <div><img src="http://oqjgod7s1.bkt.clouddn.com/googlesf.png"></div>
      <div><img src="http://oqjgod7s1.bkt.clouddn.com/githubsf.png"></div>
    </div>

    <div class="clearBoth"></div>

    <div class="register" @click="toRegister">
      注册账号
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
export default {
  name: "login",
  data() {
    return {
      mobile: "",
      password: ""
    };
  },
  methods: {
    login() {
      console.log("login");
    },
    showTips() {
      console.log("showTips");
    }
  }
};
</script>

<style lang='scss' scoped>
@import "../assets/sass/common";

.Login {
  .logo {
    img {
      margin: 30% 0 0 15%;
      width: 70%;
    }
  }

  .content {
    margin: 3rem 0 0 0;
    .phone {
      margin: 1px auto;
      width: 96%;
      text-align: center;
      input {
        width: 100%;
        height: 3rem;
        outline: none;
        border: none;
      }
      input::-webkit-input-placeholder {
        position: relative;
        left: 1rem;
        font-weight: 100;
        color: #bbb;
      }
    }
    .name {
      @extend .phone;
    }
    .password {
      @extend .phone;
    }
  }
  .regBtn {
    margin: 2rem 0 0 2%;
    width: 96%;
    button {
      border: none;
      border-radius: 0.3rem;
      font-size: 1.3rem;
      font-weight: 300;
      width: 100%;
      height: 3rem;
      outline: none;
      background: #159963;
      color: #fff;
    }
  }
  .thirdWay {
    display: flex;
    width: 70%;
    margin: 1rem 0 0 15%;
    .loginBtn {
      flex: 2;
      position: relative;
      top: 0.3rem;
      text-align: center;
      color: #969696;
      font: {
        size: 0.8rem;
        weight: 300;
      }
    }
    .line {
      float: 1;
      border-bottom: 1px solid #e5e5ee;
    }
  }
  .thirdWayLogo {
    display: flex;
    margin: 2rem 0 0 15%;
    text-align: center;
    width: 70%;
    div {
      flex: 7;
      img {
        width: 60%;
      }
    }
  }
  .register {
    position: absolute;
    bottom: 1rem;
    text-align: center;
    width: 100%;
    color: $commonColor;
  }
}
</style>



